.skill_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows:auto;

}

.underline_header{
    display: inline-block;
border-bottom: 4px solid var(--border_color);
}



@media only screen and (max-width:1320px){
    .skill_secondary h2{
        margin:0px 10px;
    }
    .skill_primary h2{
        margin:0px 10px;
    }
    .skillbadge_container{
        margin: 10px;
    }
}

@media only screen and (max-width: 1000px) {
    .skill_content {
     display: block;
    }
  }

  .skillbadge_container{
    padding:10px;
    border:1px solid var(--border_color_black);
    text-align: center;
    border-radius: 20px;
    min-width: 100px;
  }

  .skills_flex{
    display: flex;
    margin-top: 20px;
    flex-direction: row;
    flex-wrap:wrap;
    justify-content: flex-start;
    column-gap: 10px;
    row-gap: 10px;
  }