.footer_container{
    background-color: black;
    padding:120px 0px;
    text-align: center !important;
    margin-top: 100px;
}
.footer_p{
    text-transform:uppercase;
    color:#9a9898;
    font-size: 20px;
    font-weight: 600;
}

.footer_h1{
    color: white;
    font-size: 50px;
}

.footer_p2{
    color:white;
    font-size: 20px;
}


.footer_button_container{
    margin-top:30px;
    display:inline-block;
    padding: 20px;
    border: 2px double red;
    border-radius: 30px;
    cursor: pointer;
    color:red;
    width: 200px;
    letter-spacing: 4px;
    font-weight: bold;
    font-size: 20px;
}

.footer_button_container:hover {
    background-color: red;
    color:white;
}


